/* Footer */
footer {
  display: flex;
  margin: 0 auto;
  width: 100%;
  background: #111;
  color: #aaa;
  font-size: 14px;
  text-align: center;
  padding: 20px;
  display: flex;
  justify-content: center;
  // margin-bottom: 0 auto;
}

