/* Modal */
.modal {
  display: none;
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.7);
  justify-content: center;
  align-items: center;
  z-index: 999;
}

/* Modal content */
.modal-content {
  background: #2b2b2b;
  border-radius: 50px 0 50px 0;
  padding: 20px;
  position: relative;
  width: 942px;
  max-width: 95%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Modal body */
.modal-body {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  width: 100%;
}

/* Modal image */
.modal-image {
  flex: 1 1 300px;
  max-width: 400px;
  width: 100%;
  border-radius: 20px;
  object-fit: cover;
}

/* Modal details */
.modal-details {
  flex: 1 1 300px;
  min-width: 200px;
  text-align: left;
}

/* Modal close button */
.modal-close {
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 28px;
  color: white;
  cursor: pointer;
}

/* Ticket button */
.ticket-btn {
  display: inline-block;
  margin-top: 20px;
  padding: 12px 20px;
  background: #7b2ff7;
  color: #fff;
  text-decoration: none;
  border-radius: 10px;
}

/* Highlight color */
.highlight {
  color: #dc56c5;
}

