/* Responsive behavior */

/* For 1280px screens */
@media (max-width: 1280px) {
  .modal-content {
    width: 942px;
  }
}

/* For 768px screens */
@media (max-width: 768px) {
  .modal-content {
    width: 595px;
    border-radius: 20px;
  }

  .modal-body {
    flex-direction: column;
    align-items: center;
  }

  .event-card {
    width: 140px;
    height: 250px;
  }

  .event-card img {
    width: 140px;
    height: 190px;
  }
}

/* For 480px screens */
@media (max-width: 480px) {
  .event-container {
    padding: 20px 10px;
  }

  .event-card {
    width: 45%;
    height: auto;
  }

  .event-card img {
    width: 100%;
    height: auto;
  }

  .pagination {
    gap: 4px;
  }
}

/* For 320px screens */
@media (max-width: 320px) {
  .modal-content {
    width: 272px;
    border-radius: 20px;
  }

  .ticket-btn {
    width: 100%;
    text-align: center;
  }

  .modal-close {
    top: 10px;
    right: 10px;
    font-size: 24px;
  }
}

.pagination-container {
  display: flex;
  list-style: none;
  gap: 5px;
}

.pag-button {
  width: 30px;
  height: 30px;
  color: rgb(255, 255, 255);
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
  border: none;
  background: none;
}

.pag-button:hover {
  box-sizing: border-box;
  border-radius: 10px 0px 10px 0px;
  color: rgb(220, 86, 197);
  border: 1px solid rgb(220, 86, 197);
}

.pag-button.active {
  border: 1px solid rgb(220, 86, 197);
  color: rgb(220, 86, 197);
  border-radius: 10px 0px 10px 0px;
}
